 @font-face {
    font-family: 'digicon';
    src:    url('fonts/digicon.eot');
    src:    url('fonts/digicon.eot?#iefix') format('embedded-opentype'),
        url('fonts/digicon.ttf?') format('truetype'),
        url('fonts/digicon.woff?') format('woff'),
        url('fonts/digicon.svg?#digicon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'digicon' !important;
    speak: none;
    font-size: 90px;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    display: inline-block;
    margin: 0 auto;
    color: #fff;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


